html, body {
  height: 100%;
  overflow: hidden;
}

html {
  font-size: 10px;
}

body {
  background-color: #9e9e9f;
  // background-color: #222;
  display: flex;
  justify-content: center;
  align-items: center;
}

canvas {
  // max-width: 100%;
  // box-shadow: 7px 7px 14px rgba(#000, 0.4);
  border-radius: 4px;
  // position: relative;
  // z-index: 99;
}

.container {
  position: relative;
  color: #fff;
  font-family: Yantramanav, sans-serif;
  user-select: none;
}

.panel {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  &__game {
    display: none;
    height: 100%;
    position: relative;
    &-wrapper {
      &-center {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        top: 20px;
        left: 50%;
        transform: translateX(-50%);
      }
      &-right {
        position: absolute;
        top: 28px;
        right: 32px;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
      }
    }
    &-time {
      font-size: 4.8rem;
      font-weight: 500;
      margin-bottom: 2px;
    }
    &-alert, &-level {
      font-size: 1.8rem;
      color: #f5af5f;
      font-weight: 300;
      margin-left: -21.12px;
    }
    &-alert {
      margin-bottom: 4px;
      display: flex;
      align-items: center;
    }
    &-hp {
      // position: absolute;
      background-color: #f5af5f;
      // top: 28px;
      // right: 32px;
      width: 216px;
      height: 14px;
      margin-bottom: 18px;
      border-radius: 8px;
    }
    &-heart {
      position: relative;
      width: 32px;
      height: 24px;
      margin-right: 6px;
      &:before, &:after {
        content: "";
        position: absolute;
        top: 0;
        width: 16px;
        height: 24px;
        background-color: #e7465d;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
      }
      &:before {
        left: 16px;
        transform-origin: left bottom;
        transform: rotate(-45deg);
      }
      &:after {
        left: 0;
        transform-origin: right bottom;
        transform: rotate(45deg);
      }
      &-wrapper {
        display: flex;
        justify-content: flex-end;
      }
    }
  }
}

.intro {
  font-family: "Microsoft JhengHei", sans-serif;
  font-weight: 300;
  position: absolute;
  bottom: 24px;
  left: 24px;
  color: #fff;
  font-size: 1.6rem;
  line-height: 1.618;
}

.logo {
  position: absolute;
  top: 36.5%;
  left: 48%;
  font-size: 10rem;
  font-weight: 500;
}

h1 {
  position: absolute;
  top: 52%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2.4rem;
  // font-weight: 300;
  // letter-spacing: 0.4px;
}

button {
  box-shadow: 0 0 4px rgba(#fff, 0.8);
  outline: none;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translateX(-50%);
  background-color: transparent;
  border: 1px solid #fff;
  padding: 6px 20px;
  color: #fff;
  border-radius: 18px;
  font-size: 1.6rem;
  font-family: Yantramanav, sans-serif;
  cursor: pointer;
  // font-weight: 300;
  transition: background-color 0.5s ease-out, color 0.5s ease-out;
  animation: shine 1.2s infinite ease-out alternate;
  @keyframes shine {
    100% {
      box-shadow: 0 0 12px rgba(#fff, 0.8);
    }
  }
  &:hover {
    background-color: #fff;
    color: #001d2e;
    // border-radius: 8px;
  }
}

.mr-0 {
  margin-right: 0 !important;
}